body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

.title {
  justify-content: center;
  display: flex;
}

.title h1 {
  color: orange;
  margin: 0;
  font-size: 6vw;
}

.sudoku {
  justify-content: center;
  display: flex;
}

.sudoku > .board {
  display: inline-block;
}

.sudoku > .board .board-row {
  gap: 10px;
  display: flex;
}

.sudoku > .board .board-row:not(:last-child) {
  margin-bottom: 10px;
}

.sudoku > .board .board-row .box {
  border: 1px solid #000;
  display: inline-block;
}

.sudoku > .board .board-row .box .box-row {
  display: flex;
}

.tile {
  box-sizing: border-box;
  cursor: pointer;
  border: 1px solid #000;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  font-size: 24px;
  display: flex;
}

.tile.initial {
  color: #00f;
  cursor: auto;
  background-color: #ddd;
}

.tile.filled {
  background-color: #ddd;
}

.tile.active {
  border-width: 2px;
  border-color: #007bff;
}

.tile.invalid {
  background-color: #ff0000b3;
}
